.preview-bl {
    max-width: 740px;
    min-height: 390px;
    overflow: hidden;
    position: relative;
    &__slide {
        width: 740px;
        height: 390px;
        @include cover;
        position: absolute;
        top: 0;
        left: 0; } }
.preview-slider {
    width: 100%;
    height: 450px;
    overflow: hidden;
    &__slide {
        width: 100%;
        min-height: 210px;
        margin-bottom: 25px;
        @include cover; }
    &__controls {
        max-width: 170px;
        min-height: 25px;
        position: relative;
        margin: 50px auto; }
    &__control {
        position: absolute;
        cursor: pointer;
        &_l {
            left: 0; }
        &_r {
            right: 0; } } }
.icon-arrow-l-slim, .icon-arrow-r-slim {
    width: 30px;
    height: 25px;
    fill: $d-pink; }

