.video-slider {
    height: 582px;
    overflow: hidden;
    cursor: pointer;
    &__control {
        width: 100px;
        height: 100px;
        background-color: rgba(0, 0, 0, 0.6);
        border: 3px solid rgba(45, 45, 45, 0.2);
        position: absolute;
        right: 10px;
        cursor: pointer;
        &:hover {
            .icon-arrow-up-pink {
                @include s(arrow-up-pink-hover); }
            .icon-arrow-down-pink {
                @include s(arrow-down-pink-hover); }
            &:after {
                content: '';
                width: inherit;
                height: inherit;
                position: absolute;
                right: 10px;
                border: 3px solid #000; } }
        &_t {
            top: 10px;
            &:hover {
                &:after {
                    top: 10px; } } }
        &_b {
            bottom: 10px;
            &:hover {
                &:after {
                    bottom: 10px; } } } } }
.icon-arrow-up-pink {
    @include s(arrow-up-pink);
    @include vcenter; }
.icon-arrow-down-pink {
    @include s(arrow-down-pink);
    @include vcenter; }
.slick-vertical {
    .slick-slide {
        border: none; } }
