.vote {
    min-height: 1035px;
    padding: 125px 95px;
    position: relative;
    @include image('bg-vote.png');
    @include cover;
    &__img {
        width: 560px;
        min-height: 460px;
        background-position: 0 0;
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        right: 0;
        bottom: -220px; }
    &__title {
        display: inline-block;
        text-align: left;
        color: #fff;
        @include myriad-c; }
    &__inner {
        max-width: 740px; }
    &__aside {
        width: 405px;
        height: 450px;
        position: absolute;
        top: 165px;
        right: 95px;
        @include clr; }
    &__all {
        font-size: 28px;
        float: right;
        color: #fff;
        margin-top: 25px;
        @include sitka;
        letter-spacing: -5px;
        .icon {
            fill: #fff;
            vertical-align: middle; } } }
