.video {
    width: 100%;
    height: 582px;
    &__player {
        width: 100%;
        height: 100%;
        @include clr; }
    &__main {
        width: calc(100% - 345px);
        height: 100%;
        float: left;
        position: relative;
        @include cover; }
    &__aside {
        width: 345px;
        height: 100%;
        float: right;
        position: relative; }
    &__poster_main {
        position: absolute;
        width: 100%;
        height: 100%; }
    &__icon_play {
        width: 90px;
        height: 90px;
        cursor: pointer;
        opacity: 0.6;
        @include vcenter;
        @include s(play);
        &:hover {
            opacity: 1; } } }
